import { Component, OnInit } from '@angular/core';
import { BaseService } from 'src/app/services/base.service';
import { Constants, WSTypeCode } from 'src/app/data-model/constant.model';
import { Storage } from '@ionic/storage';
import { NavController, LoadingController, AlertController } from '@ionic/angular';
import { Router, ActivatedRoute } from '@angular/router';
import { ProcessModel, WIPModel } from 'src/app/data-model/base.model';
import * as moment from 'moment';
import { BasePagePage } from 'src/app/shared/base-page/base-page.page';

@Component({
  selector: 'app-wip-process-selection',
  templateUrl: './wip-process-selection.page.html',
  styleUrls: ['./wip-process-selection.page.scss'],
})
export class WipProcessSelectionPage extends BasePagePage implements OnInit {

  checkBoxList: Array<any> = [];
  isIndeterminate: boolean;
  masterCheck: boolean;
  arrProcess: Array<any> = [];
  processReqModel: ProcessModel = new ProcessModel();
  wipReqModel: WIPModel = new WIPModel(WSTypeCode.WIP);
  currentDate: string = moment().format("YYYY-MM-DD");

  constructor(
    public loadingCtrl: LoadingController, 
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    private service: BaseService,
    private route: ActivatedRoute
    ) {

    super(loadingCtrl, alertCtrl);

  }

  ngOnInit() {

    if (this.route.snapshot.data['special']) {

      this.processReqModel = this.route.snapshot.data['special'];

      this.wipReqModel.accesstoken = this.processReqModel.accesstoken;
      this.wipReqModel.date = this.standardDateTime(this.currentDate);
      this.wipReqModel.dept = this.processReqModel.dept;

      this.service.presentLoading();

      this.service.callWebService(Constants.k_GET_PROCESS, this.processReqModel, (res) => {

        this.arrProcess = JSON.parse(res.data);

        this.arrProcess.forEach(element => {

          this.checkBoxList.push({ 'process': element, 'isChecked': false })

        });

        this.service.dismissLoading();

      });

    }

  }

  checkMaster() {
    setTimeout(() => {
      this.checkBoxList.forEach(obj => {
        obj.isChecked = this.masterCheck;
      });
    });
  }

  checkEvent() {
    const totalItems = this.checkBoxList.length;
    let checked = 0;
    this.checkBoxList.map(obj => {
      if (obj.isChecked) checked++;
    });
    if (checked > 0 && checked < totalItems) {
      //If even one item is checked but not all
      this.isIndeterminate = true;
      this.masterCheck = false;
    } else if (checked == totalItems) {
      //If all are checked
      this.masterCheck = true;
      this.isIndeterminate = false;
    } else {
      //If none is checked
      this.isIndeterminate = false;
      this.masterCheck = false;
    }
  }

  standardDateTime(dateString: string) {
    return moment(dateString).format("YYYY-MM-DD");
  }

  async onNextClicked() {
    console.log("CHOSEN: ", this.checkBoxList);
    // this.navCtrl.navigateForward('/wip');

    var isCheckedAtLeastOne: boolean = false;
    this.wipReqModel.process = [];

    this.checkBoxList.forEach(element => {
      if (element.isChecked) {
        isCheckedAtLeastOne = true
        this.wipReqModel.process.push(element.process.value)
      }
    });

    if (isCheckedAtLeastOne) {
      this.service.setData('sel-proc', this.wipReqModel);
      this.navCtrl.navigateForward('/wip/sel-proc');
    } else {
      BasePagePage.presentAlert('Alert', 'Please select at least one (1) process.');
    }

  }

}
